@import url('https://fonts.googleapis.com/css2?family=DM+Sans:ital,opsz,wght@0,9..40,100..1000;1,9..40,100..1000&display=swap');

$body-font: 'DM Sans', sans-serif;

body, html {
  margin: 0;
  padding: 0;
  height: 100%;
}
.unique p{
  font-size: 16px;
  line-height: 1.5;
  font-weight: 1rem;
  color: #6F757C;
  width: 500px;
  padding-left: -20px;

}
.unique{
  background-image: url("./images/vector.svg");
  background-position:top right;
  background-repeat: no-repeat;
  width: 500px;
  margin-left: 500px;
  height: 200px;
  
.unique img{
  width: 100px;
  height: 100px;
  margin-left: 600px;
  margin-top: 50px;
}

}
.container {
  position: relative;
  width: 100%;
  height: 1000px;
}

.black-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height:1300px;
  background-color: black;
  z-index: -1;
}

.cafe-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 30vh;
  object-fit: cover;
  z-index: -1;
}

body {
    font-family: $body-font;
    font-size: 16px;
    line-height: 1.5;
    font-weight: 1rem;
    color: #333;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

h1 {
    font-size: 32px;
    font-weight: 2rem; 
}

.product-list {
    background-color: #1B1D1F;
    color: #fff;
    height: 1050px;
   padding-top: 70px;
    width: 80%;
    text-align: center;
    margin-left: 200px;
    margin-top:160px;
    z-index: 2;
    border-radius: 10px;
}
.all-products {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    margin-top: 50px;
}
.products-row-one{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    margin-top: 50px;
}
.products-row-two{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    margin-top: 50px;
}
.spans-container {
  display: flex;
  margin-top: 20px; /* Add margin between h3 and spans */
  margin-bottom: 20px;
  flex-direction: row;
  justify-content: center;
  gap: 30px;
}
button{
    color: #fff;
    background-color: #6F757C;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
    font-weight: 700;
    margin-top: 20px;
    height: 40px;
    width: 150px;
}
.available-now {
  margin-top: 50px;
}
// mobile design
@media screen and (max-width: 1025px) {
  .black-background{
    height: 2500px;
  }
    .product-list {
        width: 100%;
        height: 2100px;
    }
    .product-list {
        width: 100%;
        margin-left: 0;
    }
    .products-row-one{
        flex-direction: column;
    }
    .products-row-two{
        flex-direction: column;
    }
}